import Vue from 'vue';
var app = getApp();
export default Vue.extend({
    data: function () {
        return {
            title: 'Hello',
            type: 4,
            banner_list: '',
            token: '',
            teacher_info: {
                subject_name: {
                    title: ''
                }
            },
            teacher_infos: {},
            student_info: {},
            is_vip: '',
            vip_time: '',
            is_totul: false,
            invest: 0,
            renew: 0,
            platform: ""
        };
    },
    onLoad: function () {
        this.token = uni.getStorageSync('token');
        if (this.token == '') {
            uni.navigateTo({
                url: '/pages/login/login'
            });
        }
        this.platform = app.globalData.systemInfo.platform;
    },
    onShow: function () {
        this.token = uni.getStorageSync('token');
        this.type = uni.getStorageSync('type');
        if (uni.getStorageSync('is_vip')) {
            this.is_vip = uni.getStorageSync('is_vip');
        }
        if (this.type == 3) {
            uni.setTabBarItem({
                index: 1,
                text: '我的试卷',
                iconPath: '/static/imgs/icon/myPaper1.png',
                selectedIconPath: '/static/imgs/icon/myPaper.png'
            });
        }
        else {
            uni.setTabBarItem({
                index: 1,
                text: '错题本',
                iconPath: '/static/imgs/icon/cuotiben1.png',
                selectedIconPath: '/static/imgs/icon/cuotiben.png'
            });
        }
        this.get_banner();
        if (this.token && this.type == 3) {
            this.get_teacher();
            this.get_teacher_info();
        }
        if (this.token && this.type == 4) {
            this.get_student();
        }
        var _this = this;
        // 查看是否授权
        uni.getSetting({
            success: function (res) {
                if (res.authSetting['scope.userInfo']) {
                    // 已经授权，可以直接调用 getUserInfo 获取头像昵称
                    uni.getUserInfo({
                        success: function (res) {
                            console.log(res.userInfo);
                            res.openid = uni.getStorageSync('userInfo').openid;
                            _this.$api.update_userinfo(res).then(function (res) {
                                console.log(res);
                            });
                        }
                    });
                }
                else {
                    uni.login({
                        success: function (res) {
                            _this.$api.wx_login(res).then(function (res) {
                                console.log(res);
                            });
                        }
                    });
                }
            }
        });
    },
    onShareAppMessage: function () {
        return {
            title: 'AI错题宝',
            path: '/pages/index/index'
        };
    },
    methods: {
        get_teacher_info: function () {
            var _this = this;
            this.$api.get_teacher_info({
                token: _this.token
            }).then(function (res) {
                console.log(res, 'get_teacher_info');
                _this.teacher_infos = res.data;
            });
        },
        tobead: function () {
            if (this.teacher_infos.cert == 1) {
                this.todetail(7);
            }
            else {
                console.log('tishi');
                uni.showToast({
                    title: '您未认证无法查看校本试卷!',
                    icon: 'none'
                });
            }
        },
        tabbarshopClick: function () {
            if (app.globalData.systemInfo.platform == "ios") {
                uni.navigateTo({
                    url: "/pages/mall/iostip"
                });
            }
            else {
                uni.navigateTo({
                    url: '/pages/mall/index'
                });
            }
        },
        closeTotul: function () {
            this.is_totul = false;
        },
        setTimeType: function (timer) {
            var d = new Date(timer);
            var ConvertedYear = d.getFullYear().toString();
            var ConvertedMonth = (d.getMonth() + 1).toString();
            var ConvertedDate = d.getDate().toString();
            ConvertedMonth = ConvertedMonth.length < 2 ? '0' + ConvertedMonth : ConvertedMonth;
            ConvertedDate = ConvertedDate.length < 2 ? '0' + ConvertedDate : ConvertedDate;
            return ConvertedYear + "-" + ConvertedMonth + "-" + ConvertedDate;
        },
        pageToVip: function () {
            uni.navigateTo({
                url: '/pages/person/vip'
            });
            // if (app.globalData.systemInfo.platform == 'ios') {
            // 	uni.showToast({
            // 		title: '由于相关规范,ios功能暂不可用!',
            // 		icon: 'none'
            // 	});
            // } else {
            // 	uni.navigateTo({
            // 		url: '/pages/person/vip'
            // 	});
            // }
        },
        uploadPaper: function () {
            uni.navigateTo({
                url: '/pages/person/mineUploadPaper?id=1'
            });
        },
        toclassinfo: function () {
            uni.navigateTo({
                url: '/pages/person/classInfo'
            });
        },
        //获取学生信息
        get_student: function () {
            var _this_1 = this;
            var _this = this;
            var istol = uni.getStorageSync('istol');
            var istolbool = uni.getStorageSync('istolbool');
            _this.$api.student_index({
                token: _this.token
            }).then(function (res) {
                console.log(res);
                _this.student_info = res.data;
                _this.is_vip = res.data.is_vip;
                _this.vip_time = res.data.vip_time;
                _this.invest = res.data.invest;
                _this.renew = res.data.renew;
                if (res.data.renew && istol === new Date().getDate() && istolbool) {
                    _this.is_totul = true;
                    uni.setStorageSync('istolbool', false);
                }
                else {
                    _this.is_totul = false;
                }
                uni.setStorage({
                    key: 'is_vip',
                    data: _this_1.is_vip
                });
            });
        },
        //获取老师信息
        get_teacher: function () {
            var _this_1 = this;
            this.$api.teacher_index({
                token: this.token
            }).then(function (res) {
                console.log(res);
                _this_1.teacher_info = res.data;
            });
        },
        get_banner: function () {
            var _this_1 = this;
            this.$api.banner({
                type: this.type
            }).then(function (res) {
                console.log(res);
                _this_1.banner_list = res.data.banner_list;
            });
        },
        todetail: function (i) {
            var url = '';
            if (i == 0) {
                url = '/pages/myteaching/myteachingPhoto?from=1';
            }
            else if (i == 1) {
                url = '/pages/myteaching/contentReference';
            }
            else if (i == 2) {
                url = '/pages/myPaper/myPaper';
            }
            else if (i == 3) {
                uni.switchTab({
                    url: '/pages/errorBook/errorBook'
                });
            }
            else if (i == 4) {
                url = '/pages/learningReport/learningReport';
            }
            else if (i == 5) {
                url = '';
            }
            else if (i == 6) {
                uni.switchTab({
                    url: '/pages/errorBook/errorBook'
                });
            }
            else if (i == 7) {
                url = '/pages/myPaper/myPaper';
            }
            else if (i == 8) {
                url = '/pages/learningReport/j-xueqingbaogao';
            }
            else if (i == 9) {
                url = '/pages/myPaper/new_paper';
            }
            uni.navigateTo({
                url: url
            });
        }
    }
});
